@import (reference) "../../color";
.timezone(@main-blue-color, @main-font-color, @second-font-color, @border-color, @drop-down-background-color, @hover-background-color) {
  height: 200px;
  padding-top: 20px;
  .timezone-content {
    display: flex;
    flex-direction: row;
    align-items: center;
    color: @main-blue-color;
    font-size: 14px;
    .timezone-content-title {
      color: @second-font-color;
    }
    .timezone-content-select {
      position: relative;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      padding: 0 10px;
      margin-left: 20px;
      height: 30px;
      width: 200px;
      border: solid 1px @border-color;
      border-radius: 2px;
      cursor: pointer;
      color: @main-font-color;
      .timezone-content-select-arrow {
        border-left: solid 5px transparent;
        border-right: solid 5px transparent;
        border-top: solid 5px @main-font-color;
        transition: all .2s;
        transform-origin: center;
      }
      .timezone-content-select-drop-down-container {
        position: absolute;
        background-color: @drop-down-background-color;
        left: 0;
        z-index: 20;
        top: 30px;
        border-radius: 2px;
        width: 100%;
        padding: 2px;
        transform-origin: top;
        transform: scaleY(0);
        opacity: 0;
        transition: all .2s;
        box-shadow: 0 6px 12px 0 rgba(0, 0, 0, .5);
        height: 200px;
        overflow-y: auto;
        li {
          display: flex;
          flex-direction: row;
          align-items: center;
          padding-left: 6px;
          height: 32px;
          line-height: 32px;
          &:hover {
            background-color: @hover-background-color;
            color: @main-font-color
          }
        }
      }
    }
    .timezone-content-select:hover {
      color: @main-font-color;
      border-color: @main-blue-color;
      .timezone-content-select-arrow {
        transform: rotate(180deg);
        border-top-color: @main-font-color;
      }
      .timezone-content-select-drop-down-container {
        transform: scaleY(1);
        opacity: 1;
      }
    }
  }
}

.timezone-dark {
  .timezone(
    @main-blue-color-dark,
    @main-font-color-dark,
    @second-font-color-dark,
    @border-color-dark,
    @drop-down-background-color-dark,
    @hover-background-color-dark
  )
}

.timezone-light {
  .timezone(
    @main-blue-color-light,
    @main-font-color-light,
    @second-font-color-light,
    @border-color-light,
    @drop-down-background-color-light,
    @hover-background-color-light
  )
}
