@import (reference) "../color";

.setting-modal(@main-blue-color, @main-font-color, @second-font-color, @border-color, @drop-down-background-color) {
  position: absolute;
  margin: auto;
  background-color: @drop-down-background-color;
  border-radius: 4px;
  box-shadow: 0 6px 12px 0 rgba(0, 0, 0, .5);
  z-index: 20;
  transition: all .2s ease-in-out;
  transform-origin: center;
  opacity: 0;
  transform: scale(0);
  width: 540px;
  left: calc(50% - 270px);
  top: 100px;
  .setting-modal-title-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 48px;
    border-bottom: solid 1px @border-color;
    padding: 0 16px;
    .setting-modal-title-tab-container {
      display: flex;
      flex-direction: row;
      height: 100%;
      .setting-modal-title-tab {
        display: flex;
        flex-direction: row;
        align-items: center;
        border-top: 2px solid transparent;
        border-bottom: 2px solid transparent;
        height: 100%;
        margin-right: 30px;
        font-size: 16px;
        color: @second-font-color;
        cursor: pointer;
        transition: all .2s;
        &:hover {
          color: @main-font-color;
          border-bottom-color: @main-blue-color;
        }
        &-selected {
          border-bottom-color: @main-blue-color;
          color: @main-blue-color!important;
          font-weight: bold;
        }
      }
    }
  }
  .setting-modal-title-icon {
    font-size: 18px;
    color: @second-font-color;
    cursor: pointer;
    &:hover {
      color: @main-font-color;
    }
  }
  .setting-modal-content-container {
    padding: 0 16px;
    transition: all .2s;
  }
  .setting-modal-bottom-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    height: 60px;
    border-top: solid 1px @border-color;
    padding-right: 20px;
    button {
      height: 34px;
      width: 70px;
      font-size: 14px;
      border-radius: 2px;
      outline: none;
      cursor: pointer;
      &:hover {
        opacity: .9;
      }
      &[datatype="cancel"] {
        background-color: transparent;
        border: solid 1px @border-color;
        color: @second-font-color;
      }
      &[datatype="confirm"] {
        background-color: @main-blue-color;
        margin-left: 30px;
        color: #ffffff;
        border: none;
      }
    }
  }
}

.setting-modal-show {
  opacity: 1!important;
  transform: scale(1)!important;
}

.setting-modal-dark {
  .setting-modal(
    @main-blue-color-dark,
    @main-font-color-dark,
    @second-font-color-dark,
    @border-color-dark,
    @drop-down-background-color-dark
  )
}

.setting-modal-light {
  .setting-modal(
    @main-blue-color-light,
    @main-font-color-light,
    @second-font-color-light,
    @border-color-light,
    @drop-down-background-color-light
  )
}
