@import (reference) "../../color";

.technical-indicator-params(@main-blue-color, @main-font-color, @second-font-color, @border-color) {
  padding: 20px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  .technical-indicator-params-column {
    li {
      display: flex;
      flex-direction: row;
      align-items: center;
      height: 30px;
      .technical-indicator-params-column-row-title {
        width: 48px;
        font-size: 14px;
        color: @second-font-color;
      }
      .technical-indicator-params-column-row-input {
        border-radius: 2px;
        width: 30px;
        height: 24px;
        padding: 0 4px;
        box-sizing: border-box;
        border: solid 1px @border-color;
        margin-right: 8px;
        color: @main-font-color;
        background-color: transparent;
        outline: none;
        &:focus {
          border-color: @main-blue-color;
        }
      }
    }
  }
}

.technical-indicator-params-dark {
  .technical-indicator-params(
    @main-blue-color-dark,
    @main-font-color-dark,
    @second-font-color-dark,
    @border-color-dark
  )
}

.technical-indicator-params-light {
  .technical-indicator-params(
    @main-blue-color-light,
    @main-font-color-light,
    @second-font-color-light,
    @border-color-light
  )
}
