@import (reference) "./color";
.app(
  @main-background-color, @main-blue-color, @main-font-color, @second-font-color,
   @border-color, @drop-down-background-color, @hover-background-color
) {
  position: relative;
  height: 100%;
  background-color: @main-background-color;
  transition: all .3s;
  .github-triangle-tag {
    right: 0;
    top: 0;
    position: absolute;
    width: 0;
    height: 0;
    border-color: #151515 #151515 transparent  transparent;
    border-width: 40px 40px 40px 40px;
    border-style: solid;
    z-index: 20;
  }
  .github-icon {
    position: absolute;
    display: inline-block;
    right: 5px;
    top: 10px;
    z-index: 30;
    cursor: pointer;
    font-size: 30px;
    color: @second-font-color;
    text-underline: none;
  }
  .period-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 34px;
    border-bottom: 1px solid @border-color;
    padding-left: 8px;
    padding-right: 8px;
    span {
      font-size: 12px;
      display: inline-block;
      padding: 0 8px;
      color: @second-font-color;
      cursor: pointer;
      &:hover {
        color: @main-font-color;
      }
    }
    .period-icon {
      color: @second-font-color;
      font-size: 18px;
      cursor: pointer;
      &-selected {
        color: @main-blue-color!important;
      }
      &:hover {
        color: @main-font-color;
      }
    }
    .period-selected {
      color: @main-blue-color!important;
      font-weight: bold;
    }

    .split-line {
      display: inline-block;
      height: 16px;
      width: 1px;
      background-color: @border-color;
    }
    .select-container {
      display: flex;
      flex-direction: row;
      align-items: center;
      position: relative;
      padding: 0 10px;
      height: 34px;
      .select {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        width: 40px;
        cursor: pointer;
        span {
          padding: 0;
        }
        .select-arrow {
          border-left: solid 4px transparent;
          border-right: solid 4px transparent;
          border-top: solid 4px @second-font-color;
          transition: all .2s;
          transform-origin: center center;
        }
      }
      .select-drop-down-container {
        position: absolute;
        white-space: nowrap;
        min-width: 80px;
        background-color: @drop-down-background-color;
        left: 0;
        z-index: 20;
        top: 34px;
        border-radius: 2px;
        padding: 2px;
        transform-origin: top;
        transform: scaleY(0);
        opacity: 0;
        transition: all .2s;
        box-shadow: 0 6px 12px 0 rgba(0, 0, 0, .5);
        .select-drop-down-row {
          display: flex;
          flex-direction: row;
          align-items: center;
          padding-left: 6px;
          height: 28px;
          line-height: 28px;
          &:hover {
            background-color: @hover-background-color;
            color: @main-font-color;
          }
        }
      }
    }
    .select-container:hover {
      .select {
        .select-arrow {
          transform: rotate(180deg);
        }
      }
      .select-drop-down-container {
        transform: scaleY(1);
        opacity: 1;
      }
    }
  }
  .app-content {
    display: flex;
    flex-direction: row;
    height: calc(100% - 34px);
    .drawing-tools-container {
      height: 100%;
      display: inline-block;
      width: 56px;
      padding: 10px 8px;
      border-right: 1px solid @border-color;
      .drawing-tools-row {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 38px;
        height: 38px;
        border-radius: 4px;
        margin-bottom: 8px;
        cursor: pointer;
        font-size: 26px;
        color: @second-font-color;
        &:hover {
          background-color: @hover-background-color;
          color: @main-font-color;
        }
      }
    }
    .k-line-chart-container {
      position: relative;
      height: 100%;
      width: calc(100% - 56px);
      #k-line-chart {
        display: inline-block;
        width: 100%;
        height: calc(100% - 40px);
      }
      .technical-indicator-container {
        display: flex;
        flex-direction: row;
        padding-top: 10px;
        height: 40px;
        border-top: 1px solid @border-color;
        span {
          font-size: 14px;
          display: inline-block;
          padding: 0 8px;
          color: @second-font-color;
          cursor: pointer;
          &:hover {
            color: @main-font-color;
          }
        }
        .technical-indicator-selected {
          color: @main-blue-color!important;
          font-weight: bold;
        }
      }
    }
  }
}

.app-dark {
  .app(
    @main-background-color-dark,
    @main-blue-color-dark,
    @main-font-color-dark,
    @second-font-color-dark,
    @border-color-dark,
    @drop-down-background-color-dark,
    @hover-background-color-dark
  )
}

.app-light {
  .app(
    @main-background-color-light,
    @main-blue-color-light,
    @main-font-color-light,
    @second-font-color-light,
    @border-color-light,
    @drop-down-background-color-light,
    @hover-background-color-light
  )
}
